import React from 'react';
import {SidebarContainer, Icon, CloseIcon, SidebarWrapper, SidebarMenu, SidebarLink, SidebarBtnWrap, SidebarRoute} from './SidebarElements';

const Sidebar = ({isOpen, toggle}) => {
  return (
    <SidebarContainer isOpen={isOpen} onClick={toggle}>
      <Icon onClick={toggle}>
        <CloseIcon /> 
      </Icon>
      <SidebarWrapper>
        <SidebarMenu>
          <SidebarLink to ="overview" onClick={toggle}>Overview</SidebarLink>
          <SidebarLink to ="perception" onClick={toggle}>Enviromental Perception</SidebarLink>
          <SidebarLink to ="aipipeline" onClick={toggle}>AI Pipeline</SidebarLink>
          <SidebarLink to ="partners" onClick={toggle}>Partners</SidebarLink>
        </SidebarMenu>
        <SidebarBtnWrap>
          <SidebarRoute to ='/Videos'>Videos</SidebarRoute>
        </SidebarBtnWrap>
      </SidebarWrapper>
    </SidebarContainer>
  )
}

export default Sidebar