import styled from 'styled-components';
import {Link as LinkRouter} from 'react-router-dom';
import {Link as LinkScroll} from 'react-scroll';

import { NAVBAR_HEIGHT, COLOR_HIGHLIGHT, COLOR_LIGHT, COLOR_DARK, MEDIA_MAX_WIDTH_SMALL, MEDIA_MAX_WIDTH_MEDIUM, COLOR_AITF_GREEN } from '../variables/Variables';



export const Nav = styled.nav`
  background: ${COLOR_DARK};
  height: ${NAVBAR_HEIGHT};
  margin-top: -${NAVBAR_HEIGHT};
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  position: sticky;
  top: 0;
  z-index: 10;

  @media screen and (max-width: ${MEDIA_MAX_WIDTH_MEDIUM}) {
    transition: 0.8s all ease;
  }
`;

export const NavbarContainer = styled.div `
  display: flex;
  justify-content: space-between;
  height: ${NAVBAR_HEIGHT};
  z-index: 1;
  width: 100%; 
  padding: 0 24px;
  max-width: 1100px;
`;

export const NavLogo = styled.img`
  color: ${COLOR_LIGHT};
  width: auto;
  height: ${NAVBAR_HEIGHT};
  cursor: pointer;
  align-items: center;
  margin-left: 0px;
`;

export const MobileIcon = styled.div`
  display: none;
  
  @media screen and (max-width: ${MEDIA_MAX_WIDTH_SMALL}){
    display:block ;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: ${COLOR_LIGHT};
  }
`;

export const NavMenu = styled.ul`
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
  margin-right: 22px;

  @media screen and (max-width: ${MEDIA_MAX_WIDTH_SMALL}) {
    display: none;
  }
`;

export const NavItem = styled.li`
  height: ${NAVBAR_HEIGHT};
`;

export const NavLinks = styled(LinkScroll)`
  color: ${COLOR_LIGHT};
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
  cursor: pointer;

  &.active {
    border-bottom: 3px solid ${COLOR_HIGHLIGHT};
  }
  &:hover{
    border-top: 5px solid ${COLOR_HIGHLIGHT};
  }
`;

export const NavBtn = styled.nav`
  display: flex;
  align-items:center;

  @media screen and (max-width: ${MEDIA_MAX_WIDTH_SMALL}){
    display: none;
  }
`

export const NavBtnLink = styled(LinkRouter)`
  border-radius: 50px;
  background-color: ${COLOR_AITF_GREEN};
  white-space: nowrap;
  padding: 10px 22px;
  color: ${COLOR_HIGHLIGHT};
  font-size: 16px;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;

  &:hover {
    transition: all 0.2s ease-in-out;
    background: ${COLOR_AITF_GREEN};

  }
`
